import {
    ListData
} from 'common/';


module.exports = {
    name: 'article-list',
    components: {
        ListData
    },
    data() {
        return {
            types:{
                'A':'',
                'B':'',
                'C':'',
                'D':''
            },
            list: [{
                id:1,
                name:'',
                type:'A',
                image:'afdf',
                create_time:'2016-2-23',
                status:1
            },{
                id:2,
                name:'',
                type:'B',
                image:'fdfdf',
                create_time:'2016-3-23',
                status:2
            },{
                id:3,
                name:'',
                type:'C',
                image:'dsds',
                create_time:'2016-4-23',
                status:1
            },{
                id:3,
                name:'',
                type:'D',
                image:'dsds',
                create_time:'2016-4-23',
                status:2
            }],
            fields: [{
                type:'image',
                key: 'image',
                label: ''
            },{
                key: 'name',
                label: ''
            },{
                key: 'create_time',
                label: ''
            }, {
                key: 'type',
                label: '',
                formatter: (item)=> {
                    return this.types[item.type];
                },
                filter_list: [{
                    text: '',
                    value: 'A'
                }, {
                    text: '',
                    value: 'B'
                }, {
                    text: '',
                    value: 'C'
                }, {
                    text: '',
                    value: 'D'
                }],
                filter_method: function(value, item) {
                    return item.type == value;
                },
                filter_multiple: true,
            },  {
                key: 'status',
                label: '',
                formatter: (item)=> {
                    return item.status == 1 ? '' : '';
                },
                filter_list: [{
                    text: '',
                    value: 1
                }, {
                    text: '',
                    value: 2
                }],
                filter_method: function(value, item) {
                    return item.status == value;
                },
                filter_multiple: false,
            }],
            // btn_info: {
            //     show: false
            // },
            // expand:{
            //     show:true,
            //     position:'right'
            // }
        }
    },
    methods: {
        
    },
    mounted() {

    },
    '$route' (to, from) {

    }
}



