import {
    FormData
} from 'common/';

module.exports = {
    components: {
        FormData
    },
    data() {
        return {
            fields: [{
                label: '',
                key: 'textarea',
                type: 'textarea',
                value: 'textarea',
                desc: '',
            }],
        }
    },
    methods: {
        onSubmit(data) {
            this.$emit('onTest', data);
        }
    }
}


