module.exports = {
    name: 'list',
    data() {
        return {
            tableData: [{
                date: '1992-01-26',
                name: '',
                sex: 1,
                status: 1,
                address: '',
            }, {
                date: '1996-05-02',
                name: '',
                sex: 1,
                status: 1,
                address: '',
            }, {
                date: '1990-05-04',
                name: '',
                sex: 2,
                status: 2,
                address: '',
            }, {
                date: '1998-05-01',
                name: '',
                sex: 1,
                status: 1,
                address: '',
            }],
            selectData: {
                name: '',
                address: '',
                date: ''
            },
            fields: {
                sex: {
                    info: {
                        prop: 'sex',
                        label: '',
                        sortable: true
                    },
                    filter: {
                        list: [{
                            text: '',
                            value: 1
                        }, {
                            text: '',
                            value: 2
                        }],
                        multiple: false
                    },
                    style: {
                        width: '130',
                        align: 'center'
                    }
                },
                status: {
                    info: {
                        prop: 'status',
                        label: '',
                        sortable: true
                    },
                    filter: {
                        list: [{
                            text: '',
                            value: 1
                        }, {
                            text: '',
                            value: 2
                        }],
                        multiple: false
                    },
                    style: {
                        width: '130',
                        align: 'center'
                    }
                },
                address: {
                    info: {
                        prop: 'address',
                        label: ''
                    },
                    filter: {

                    },
                    style: {
                        align: 'center'
                    }
                },
                date: {
                    info: {
                        prop: 'date',
                        label: '',
                        sortable: true
                    },
                    filter: {

                    },
                    style: {
                        width: '180',
                        align: 'center'
                    }
                },
                name: {
                    info: {
                        prop: 'name',
                        label: '',
                        sortable: true
                    },
                    filter: {

                    },
                    style: {
                        width: '150',
                        align: 'center'
                    }
                }
            }
        }
    },
    methods: {
        /**
         * 
         */
        formatterSex(item) {
            return item.sex == 1 ? '' : '';
        },

        /**
         * 
         */
        formatterStatus(item) {
            return item.status == 1 ? '' : '';
        },

        filterSex(value, item) {
            return item.sex == value;
        },
        filterStatus(value, item) {
            return item.status == value;
        },

        onSelectData() {

        }
    }
}


