import {
    Default as Statis
} from 'common/Echarts/Bar/';

module.exports = {
    name: 'echarts',
    components: {
        Statis
    },
    data() {
        return {
            echarts_data: {
                id: 'orderBarDefault',
                title: '',
                subtext: '',
                hover_title: '',
                text_list: ["", "", "", "", "", ""],
                value_list: [0, 0, 0, 0, 0, 0]
            }
        }
    },
    methods: {
        onUpdateTitle() {
            this.echarts_data.title = '';
        },
        onUpdateSubtext() {
            this.echarts_data.subtext = '';
        },
        onUpdateHoverTitle() {
            this.echarts_data.hover_title = '';
        },
        onUpdateValueList() {
            this.getStatis();
        },
        onUpdateTextList() {
            for (var i = 0; i < this.echarts_data.text_list.length; i++) {
                this.$set(this.echarts_data.text_list, i, this.echarts_data.text_list[i] + '!');
            }
        },

        getStatis() {
            this.$$api_order_statisOrder({}, (data) => {
                for (var f in data.statis) {
                    this.$set(this.echarts_data.value_list, f - 1, data.statis[f]);
                }
            })
        }
    },
    mounted: function() {}
}


