module.exports = {
    name: 'order-list',
    data() {
        return {
            article_list: [],

            format_status_list: {
                1: '',
                2: '',
                3: '',
                4: '',
                5: '',
                6: ''
            },

            batch_id: '', //id
            batch_flag: true, //true,false

            //
            paginations: {
                current_page: 1,
                total: 0,
                page_size: 12,
                page_sizes: [3, 9, 12, 24],
                layout: "total, sizes, prev, pager, next, jumper"
            },

            fields: {
                status: {
                    info: {
                        prop: 'status',
                        label: '',
                        sortable: true
                    },
                    filter: {
                        list: [{
                            text: '',
                            value: 1
                        }, {
                            text: '',
                            value: 2
                        }, {
                            text: '',
                            value: 3
                        }, {
                            text: '',
                            value: 4
                        }, {
                            text: '',
                            value: 5
                        }, {
                            text: '',
                            value: 6
                        }],
                        multiple: false
                    },
                    style: {
                        width: '180',
                        align: 'center'
                    }
                },
                create_time: {
                    info: {
                        prop: 'create_time',
                        label: '',
                        sortable: true
                    },
                    filter: {

                    },
                    style: {
                        width: '260',
                        align: 'center'
                    }
                },
                name: {
                    info: {
                        prop: 'name',
                        label: '',
                        sortable: true
                    },
                    filter: {

                    },
                    style: {
                        width: '150',
                        align: 'center'
                    }
                }
            }
        }
    },
    methods: {

        /**
         * 
         */
        formatterStatus(item) {
            return this.format_status_list[item.status];
        },
        filterStatus(value, item) {
            return this.format_status_list[value] == this.format_status_list[item.status];
        },


        /**
         * 
         * @param {string} field 
         * @param {string} value 
         */
        setPath(field, value) {
            var path = this.$route.path,
                query = Object.assign({}, this.$route.query);

            if (typeof field === 'object') {
                query = field;
            } else {
                query[field] = value;
            }

            this.$router.push({
                path,
                query
            });
        },


        /**
         * 
         * @param  {number} page 
         */
        onChangeCurrentPage(page) {
            this.getList({
                page,
                fn: () => {
                    this.setPath('page', page);
                }
            });
        },


        /**
         * 
         * @param  {number} size 
         */
        onChangePageSize(page_size) {
            this.getList({
                page_size,
                fn: () => {
                    this.setPath('page_size', page_size);
                }
            });
        },


        /**
         * 
         * @param  {number} options.page      
         * @param  {number} options.page_size 
         * @param  {function} options.fn                            } 
         */
        getList({
            page,
            page_size,
            where,
            fn
        } = {}) {

            var query = this.$route.query;

            this.paginations.current_page = page || parseInt(query.page) || 1;
            this.paginations.page_size = page_size || parseInt(query.page_size) || this.paginations.page_size;

            var data = {
                page: this.paginations.current_page,
                page_size: this.paginations.page_size
            };

            if (where) {
                data = Object.assign(data, where || {});
            }


            this.$$api_order_selectOrder(data, (article_data) => {
                this.article_list = article_data.list.data;
                this.paginations.total = article_data.list.total;

                fn && fn();
            });
        },
    },
    mounted() {
        this.getList({
            fn: () => {
                // this.onSelectArticle(this.article_list[1]);
            }
        });
    },
    '$route' (to, from) {

    }
}


