import {
    FormData
} from 'common/';

module.exports = {
    components: {
        FormData
    },
    data() {
        return {
            fields: [{
                label: '',
                type: 'date',
                key: 'create_time',
                placeholder: '',
                value: '',
            }, {
                label: '',
                type: 'daterange',
                key: 'create_time_range_init',
                placeholder: '-',
                value: {
                    default: []
                },
            }, {
                label: '',
                type: 'year',
                key: 'create_year',
                placeholder: '',
                value: '',
            }, {
                label: '',
                type: 'month',
                key: 'create_month',
                placeholder: '',
                value: '',
            }, {
                label: '',
                type: 'week',
                key: 'create_week',
                placeholder: '',
                value: '',
            }],

        }
    },
    methods: {
        onSubmit(data) {
            this.$emit('onTest', data);
        }
    }
}


