import {
	FormData
} from 'common/';

module.exports = {
	name      : 'form-select',
	components: {
		FormData
	},
	data() {
		return {
			fields: [{
				key  : 'cate',
				type : 'select',
				multiple: false,
				value: {
					default: '',
					list   : [{
						value: 'jishu',
						text : ''
					}, {
						value: 'sanwen',
						text : ''
					}, {
						value: 'qita',
						text : ''
					}]
				},
				desc : '',
				label: ''
			}, {
				key     : 'tabs',
				type    : 'select',
				multiple: true,
				value   : {
					default: ['javascript', 'html'],
					list   : [{
						value: 'html',
						text : 'HTML'
					}, {
						value: 'css',
						text : 'CSS'
					}, {
						value: 'javascript',
						text : 'JavaScript'
					}, {
						value: 'jquery',
						text : 'query'
					}]
				},
				desc    : '',
				label   : ''
			}],
		}
	},
	methods   : {
		onSubmit(data) {
			this.$emit('onTest', data);
		}
	},
	mounted(){
		// this.fields[0].value.list.push({
		// 	text:'',
		// 	value:'test'
		// });

		// this.fields[0].value.list=[{
		// 	text:'',
		// 	value:'test'
		// }];
	}
}


