<template>
    <div class="form">
        <el-form label-width="100px" style="margin:20px;width:90%;min-width:600px;"
            :model="form_data"
            :rules="form_rules"
            ref='refForm'>
            <el-form-item label="" prop='name' style="width:600px;">
                <el-input v-model="form_data.name"></el-input>
            </el-form-item>
            <el-form-item label="" prop='status'>
                <el-select v-model="form_data.status" placeholder="">
                    <el-option value='1' label=""></el-option>
                    <el-option value='2' label=""></el-option>
                    <el-option value='3' label=""></el-option>
                    <el-option value='4' label=""></el-option>
                    <el-option value='5' label=""></el-option>
                    <el-option value='6' label=""></el-option>
                </el-select>
            </el-form-item>
            <el-form-item>
                <el-button type="primary" @click='onSubmit("refForm")'>{{form_data.id ? '' : ''}}</el-button>
                <el-button @click='reset_form("form_data")'></el-button>
            </el-form-item>
        </el-form>
    </div>
</template>
<script>
    import EditJs from './Edit.js';
    module.exports=EditJs;
</script>
<style scoped>
    
</style>



