<template>
    <div class="form">
        <el-form label-width="100px" style="margin:20px;width:90%;min-width:600px;"
            :model="article_data"
            :rules="rules"
            ref='refArticle'>
            <el-form-item label="" prop='title' style="width:600px;">
                <el-input v-model="article_data.title"></el-input>
            </el-form-item>
            <el-form-item label="" style="width:986px;" prop='content'>
                <div id="article" style="height:260px;"></div>
            </el-form-item>
            <el-form-item label="" prop='cate'>
                <el-select v-model="article_data.cate" placeholder="">
                    <el-option value=""></el-option>
                    <el-option value=""></el-option>
                    <el-option value=""></el-option>
                </el-select>
            </el-form-item>
            <el-form-item label="" prop='tabs'>
                <el-select v-model="article_data.tabs" multiple placeholder="">
                    <el-option value="html"></el-option>
                    <el-option value="css"></el-option>
                    <el-option value="javascript"></el-option>
                    <el-option value="jquery"></el-option>
                    <el-option value="vue"></el-option>
                    <el-option value="react"></el-option>
                    <el-option value="angular"></el-option>
                    <el-option value="php"></el-option>
                </el-select>
            </el-form-item>
            <el-form-item label="">
                <el-switch on-text="" off-text="" v-model="article_data.status" style="width:100%"></el-switch>
            </el-form-item>
            <el-form-item>
                <el-button type="primary" @click='onSubmit("refArticle")'>{{article_data.id ? '' : ''}}</el-button>
                <el-button @click='reset_article("article_data")'></el-button>
            </el-form-item>
        </el-form>
    </div>
</template>
<script>
    import EditJs from './Edit.js';
    module.exports=EditJs;
</script>
<style scoped>
    
</style>



