<template>
    <section class="chart">
        <el-row>
            <el-col :span='24'>
                <el-button
                    type="primary"
                    @click='onUpdateTitle'></el-button>
                <el-button
                    type="primary"
                    @click='onUpdateSubtext'></el-button>
                <el-button
                    type="primary"
                    @click='onUpdateHoverTitle'></el-button>
                <el-button
                    type="primary"
                    @click='onUpdateValueList'></el-button>
                <el-button
                    type="primary"
                    @click='onUpdateTextList'></el-button>
            </el-col>

            <el-col :span="24">
                <statis
                	:id='echarts_data.id'
                	:title='echarts_data.title'
                	:subtext='echarts_data.subtext'
                	:hoverTitle='echarts_data.hover_title'
                	:dataList='echarts_data.data_list'></statis>
            </el-col>
        </el-row>
    </section>
</template>
<script>
    import PieJs from './Pie.js';
    module.exports=PieJs;
</script>
<style scoped>
    
</style>



