module.exports = {
    name: 'list-data',
    data() {
        return {
            batch_flag: true, //true,false
            batch_datas: [],
            batch_ids: [],

            image_host: this.ImageHost, //

            list: this.List, //
            fields: this.FieldList, //
            selection: this.Selection, //
            expand:this.Expand,
            btn_info: this.BtnInfo,

            pagination: this.Pagination,
        }
    },
    methods: {
        /**
         * CheckBox
         * @param  {array} val 
         */
        onSelectionChange(val) {
            this.batch_datas = val;

            this.batch_ids = [];
            if (val.length) {
                this.batch_flag = false;
                for (var i = 0; i < val.length; i++) {
                    this.batch_ids.push(val[i].id);
                }
            } else {
                this.batch_flag = true;
            }

            /**
             * CheckBoxID
             */
            this.$emit('onSelectionChange', this.batch_ids, this.batch_datas);
            this.$emit('onSelectionChangeObj', {
                ids: this.batch_ids,
                datas: this.batch_datas
            });
        },



        /**
         * 
         * @param  {object || boolean} user  ,
         * @param  {number} index 
         */
        onDelete(data, index) {
            var opts = {};
            if (data === true) {
                opts.batch_ids = this.batch_ids;
                opts.batch_datas = this.batch_datas;
            } else {
                opts.data = data;
                opts.index = index;
            }

            /**
             * 
             * 
             * 1
             *     opts.data 
             *     opts.index 
             *     opts.list 
             * 2
             *     opts.batch_ids ID
             *     opts.batch_datas ()
             */
            this.$emit('onDelete', opts);
        },

        onAdd(){
            this.$emit('onAdd', {});
        },

        /**
         * 
         * @param  {object} row   
         * @param  {number} index 
         * @param  {array} list  
         */
        onGetInfo(row, index, list, type) {
            this.$emit('onGetInfo', {
                row,
                index,
                list,
                type
            });
        },


        onBtnEvent(type,row={},index=-1,list=[]){
            this.$emit('onBtn'+type, {
                row,
                index,
                list
            });
        },


        onUpdateBtn(data, index, list) {
            if (this.btn_info.update && this.btn_info.update.path) {
                var path = this.btn_info.update.path,
                    param_keys = this.btn_info.update.param_keys || [],
                    query_keys = this.btn_info.update.query_keys || [],
                    query = {};

                for (var i = 0; i < param_keys.length; i++) {
                    path += '/' + data[param_keys[i]];
                }
                for (var i = 0; i < query_keys.length; i++) {
                    query[query_keys[i]] = data[query_keys[i]];
                }

                // console.log(path);
                // console.log(query);

                this.$router.push({
                    path: path,
                    query: query
                });
            } else {
                this.onGetInfo(data, index, list, 'update');
            }

        },


        /**
         * 
         * 
         * 1
         *     row 
         * 2
         *     row ID
         */
        onUpdateList(row) {
            if (!Array.isArray(row)) {
                this.list.splice(row, 1);
            } else {
                this.list = this.list.filter(function(item, idx) {
                    return row.indexOf(item.id) === -1;
                });
            }
        },


        /**
         * 
         * @param  {number} page 
         */
        onChangeCurrentPage(page) {
            this.$emit('onChangeCurrentPage', page);
        },


        /**
         * 
         * @param  {number} page_size 
         */
        onChangePageSize(page_size) {
            this.$emit('onChangePageSize', page_size);
        }
    },

    mounted() {
        // console.log(this.list);
    },

    /**
     * 
     * @type {Object}
     */
    props: {
        ImageHost: {
            type: String,
            default: ''
        },
        List: {
            type: Array,
            required: true
        },
        FieldList: {
            type: Array,
            required: true
        },
        BtnInfo: {
            type: Object,
            default () {
                return {};
            }
        },
        Selection: {
            type: Boolean,
            default: false
        },
        Expand: {
            type: Object,
            default(){
                return {
                    show:false,
                    position:"left"
                };
            }
        },
        Pagination: {
            type: Object,
            default () {
                return {};
            }
        }
    },


    /**
     * 
     * @type {Object}
     */
    watch: {
        ImageHost(v) {
            if (v) {
                this.image_host = v;
            }
        },
        List(v) {
            if (v) {
                this.list = v;
            }
        },
        FieldList(v) {
            if (v) {
                this.fields = v;
            }
        },
        Selection(v) {
            this.selection = v;
        },
        Expand(v){
            this.expand=v;
        },
        BtnInfo(v) {
            this.btn_info = v;
        },
        Pagination(v) {
            this.pagination = v;
        }
    }
}


