module.exports = {
    name: 'cart-list',
    computed: {
        cartList() {
            // console.log('cart.vue computed cartlist');
            // console.log(this.$store.getters.getCartList);

            return this.$store.getters.getCartList
        }
    },

    methods: {

        getCartIndex(id) {
            var index = -1,
                list = this.cartList;
            for (var i = 0; i < list.length; i++) {
                if (list[i].id == id) {
                    index = i;
                    break;
                }
            }
            return index;
        },

        /**
         * 
         * @param  {number} index 
         * @param  {string} type  mutations
         * @param  {string} type=add_db  
         * @param  {string} type=reduce_db  
         * @param  {string} type=delete_db  
         */
        action_cart(cart, type) {
            var index = this.getCartIndex(cart.id);

            //
            this.$store.dispatch('update_cur_shop_status', {
                index
            });

            //state
            this.$store.dispatch(type);
        }
    },
    mounted() {

    }
}


