import {
    ListData,
    DialogInfo
} from 'common/';


module.exports = {
    name: 'article-list',
    components: {
        ListData,
        DialogInfo
    },
    data() {
        return {
            list: [],
            fields: [{
                key: 'create_time',
                label: ''
            }, {
                key: 'title',
                label: ''
            }, {
                key: 'status',
                label: '',
                formatter: function(item) {
                    return item.status == 1 ? '' : '';
                },
                filter_list: [{
                    text: '',
                    value: 1
                }, {
                    text: '',
                    value: 2
                }],
                filter_method: function(value, item) {
                    return item.status == value;
                },
                filter_multiple: false,
            }],
            btn_info: {
                // show: false,
                label: '',
                width: 300,
                // delete: false,
                // select: false,

                //
                //pathonGetInfo
                //path=>
                //query_keys=>
                update: {
                    path: '/adv/article/edit',
                    // param_keys: ['id', 'status'],
                    query_keys: ['id', 'status']
                },

                // list: [{
                //     text: '',
                //     fn_type: 'access'
                // }, {
                //     text: '',
                //     fn_type: 'status'
                // }]
            },

            pagination: {
                current_page: 1,
                total: 0,
                page_size: 12,
                page_sizes: [3, 9, 12, 24],
                layout: "total, sizes, prev, pager, next, jumper"
            },


            apis: {
                method: {
                    get_list: '$$selectArticle',
                    delete_data: '$$deleteArticle'
                },
                route: {
                    update_path: '/adv/article/edit'
                }
            }

        }
    },
    methods: {
        init() {
            this.getDataList();
        }
    },
    mounted() {
        this.init();
    },
    '$route' (to, from) {

    }
}


